import React from 'react';
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Image from 'react-bootstrap/Image'
import Activities from '../../assets/img/profile/activities.jpg'
import Activities2 from '../../assets/img/profile/activities2.jpg'
import ListGroup from 'react-bootstrap/ListGroup'
import './extra-activities.styles.css'

const ExtraActivities = () => {
    return (
      <div id="activities">
        <div className="about">
        <h1 className="pt-3 text-center font-details-b pb-3">Extra Curriculam Activities</h1>
          <Container>
            <Row className="pt-3 pb-5 align-items-center">
              <Col xs={12} md={6}>
                <Row className=" align-items-start p-2 my-details rounded">
                <strong> Publication</strong>
            Md. Sham-E-Ansari, Shaminaj Towfika Disha, Atiqul Islam Chowdhury, Md. Khairul Hasan, A neural Network Based Approach for Recognition of Basic Emotions from Speech, Presented in IEEE TENSYMP ( 2020 IEEE Region 10 Symposium) June 2020, Bangladesh. 
            <a href="https://ieeexplore.ieee.org/document/9230641">Click to read the full paper</a>
            <br />
            <br/>
                <ListGroup variant='flush'>
                    <ListGroup.Item>Participated in <strong>BUP ICT FEST 2018</strong> in hackathon category and Achieved <strong>1st runner up position</strong>.</ListGroup.Item>
                    <ListGroup.Item>Participated Mindsparks Project show casing in software category,2019.</ListGroup.Item>
                    <ListGroup.Item>Participated DIU App Contest,2017</ListGroup.Item>
                    <ListGroup.Item>Participated in INNOVENTURE17 (Category: Software).</ListGroup.Item>
                    <ListGroup.Item>Participated every semester Intra Aust Programming Contest.</ListGroup.Item>
                </ListGroup>
                </Row>
              </Col>
              <Col xs={12} md={6}>
                <Row className="justify-content-center mb-2">
                  <Image className="profile justify-content-end" alt="activity" src={Activities} thumbnail fluid />
                </Row>
                <Row className="justify-content-center mb-2">
                  <Image className="profile justify-content-end" alt="activity" src={Activities2} thumbnail fluid />
                </Row>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
    );
  };
  
  export default ExtraActivities;