import React from 'react';
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Image from 'react-bootstrap/Image'
import Profile from '../../assets/img/profile/profile.jpg'
import Button from "react-bootstrap/Button";
import ListGroup from 'react-bootstrap/ListGroup'
import './about.style.css'

const About = () => {
    return (
      <div id="about">
        <div className="about">
          <h1 className="pt-3 text-center font-details pb-3">ABOUT ME</h1>
          <Container>
            <Row className="pt-3 pb-5 align-items-center">
              <Col xs={12} md={6}>
                <Row className="justify-content-center mb-2">
                  <Image className="profile justify-content-end" alt="profile" src={Profile} thumbnail fluid />
                </Row>
              </Col>
              <Col xs={12} md={6}>
                <Row className=" align-items-start p-2 my-details rounded">
                    Hi, I'm<strong> &nbsp;Sham E Ansari</strong> <br></br>
                Former student of Computer Science and Engineering in Ahsanullah University of Science and Technology, Bangladesh. My aim is to be associated with an organization that provides career development opportunities as a Software Developer and an environment to enable me to contribute in its growth. Currently I'm working as a Junior Web Developer in a multi-national company named GraphicPeople.  I'm a quick learner. I'm good at Database Design and Database management system and have a good knowledge about Android application development and Web development.
                
                <ListGroup variant='flush'>
                    <ListGroup.Item>Solved over 200+ problems in online judge like Uri, UVa, Codeforces.</ListGroup.Item>
                    <ListGroup.Item>Knowledge of Machine Learning.</ListGroup.Item>
                    <ListGroup.Item>Achieved Gold badge in SQL and JS, Silver badge in problem solving in Hackerrank</ListGroup.Item>
                    <ListGroup.Item>Completed several online courses about JS, Android development, Data Science in Udemy, Coursera.</ListGroup.Item>
                    <ListGroup.Item>Have experience in participating android app hackathon and achieved 1st runner up position in BUP ICT FEST 2018.</ListGroup.Item>
                </ListGroup>
                  <br /> <br />
                  <Col className="d-flex justify-content-center flex-wrap">
                    <div>
                      <a href="#contact">
                        <Button className="m-2" variant="outline-primary">
                          Contact Me
                        </Button>
                      </a>
                    </div>
                    <div>
                      <a href="https://drive.google.com/file/d/1E5B_F7LfOwVFKJ6PAZOL7bTdzB8ctdSY/view?usp=sharing" target="_blank" rel="noopener noreferrer">
                        <Button className="m-2" variant="outline-success">
                          My Resume
                        </Button>
                      </a>
                    </div>
                    <div>
                      <a href="https://github.com/sham-e-ansari" target="_blank" rel="noopener noreferrer">
                        <Button className="m-2" variant="outline-dark">
                          GitHub
                        </Button>
                      </a>
                    </div>
                    <div>
                      <a href="https://www.linkedin.com/in/sham-e-ansari/" target="_blank" rel="noopener noreferrer">
                        <Button className="m-2" variant="outline-info">
                          LinkedIn
                        </Button>
                      </a>
                    </div>
                  </Col>
                </Row>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
    );
  };
  
  export default About;