import React from 'react';
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Jumbotron from "react-bootstrap/Jumbotron";
import Card from "react-bootstrap/Card";
import Tilt from "react-tilt";
import './education.styles.css'

const Education = () => {
    return (
      <div id="education">
        <div className="education">
          <h1 className="pt-3 text-center font-details pb-3">Education</h1>
        <Jumbotron className="jumbo-style-edu">
            <Container>
                <Tilt options={{ max: 25 }}>
                <Card>
                <Card.Text className="text-center style">
                <br/>
                <Row>
                    <Col>
                        <strong>Ahsanullah University of Science and Technology</strong>
                    </Col>
                </Row>
                <br/>
                <Row>
                    <Col className="eduDetails"><strong>Degree: </strong> BSc in CSE
                    </Col>
                    <Col><strong>Degree: </strong> CGPA: 3.42
                    </Col>
                    <Col><strong>Year: </strong>2015-2019
                    </Col>
                </Row>
                <br/>
                <br/>
                <Row>
                    <Col>
                        <strong>Police Lines School and College, Rangpur</strong>
                    </Col>
                </Row>
                <br/>
                <Row>
                    <Col className="eduDetails"><strong>Degree: </strong>HSC
                    </Col>
                    <Col><strong>Degree: </strong> CGPA: 5.00
                    </Col>
                    <Col><strong>Year: </strong>2011-2013
                    </Col>
                </Row>
                <br/>
                <br/>
                <Row>
                    <Col>
                        <strong>Rangpur Zilla School, Rangpur</strong>
                    </Col>
                </Row>
                <br/>
                <Row>
                    <Col className="eduDetails"><strong>Degree: </strong>SSC
                    </Col>
                    <Col><strong>Degree: </strong> CGPA: 5.00
                    </Col>
                    <Col><strong>Year: </strong>2011
                    </Col>
                </Row>  
                <br/>
                </Card.Text>  
                </Card>
          </Tilt>
          
        </Container>
      </Jumbotron>
        </div>
      </div>
    );
  };
  
  export default Education;