import React from "react";
import Container from "react-bootstrap/Container";
import Jumbotron from "react-bootstrap/Jumbotron";
import Card from "react-bootstrap/Card";
import L_GP from "../../assets/img/experience/gp.png";
import L_SAIR from "../../assets/img/experience/sair.png";
import Tilt from "react-tilt";
import "./experience.styles.css";

const Experience = () => {
  return (
    <div id="experience">
      <h1 className="pt-3 text-center font-details-b pb-3">EXPERIENCE</h1>
      <Jumbotron className="jumbo-style">
        <Container>
          <Tilt options={{ max: 25 }}>
            <Card>
              <Card.Header as="h5" className="d-flex justify-content-center flex-wrap">
                <Card.Img variant="top" className="img-resize" src={L_GP} alt="GraphicPeople logo" />
              </Card.Header>
              <Card.Body className="d-flex justify-content-center flex-column">
              <div>
                  <Card.Title className="text-center">Junior Web Developer</Card.Title>
                </div>
                <div>
                  <Card.Text className="text-center style">
                    <strong>Duration:</strong> February 2021 - Present
                  </Card.Text>
                </div>
                <br />
                <br />
                <div>
                  <Card.Title className="text-center">Trainee Web Developer</Card.Title>
                </div>
                <div>
                  <Card.Text className="text-center style">
                    <strong>Duration:</strong> June 2020 - January 2021
                    <br/>
                    <br />
                    <ul className="text-left">
                      <li><strong>Technology:</strong> React JS, AEM Framework</li>
                      <li><strong>Directly</strong> involved in company projects.</li>
                      {/* <li><strong>Co-created</strong> React Document used as a guide for new developers.</li> */}
                    </ul>
                    
                    
                  </Card.Text>
                </div>
              </Card.Body>
            </Card>
          </Tilt>
          <br/>
          <Tilt options={{ max: 25 }}>
            <Card>
              <Card.Header as="h5" className="d-flex justify-content-center flex-wrap">
                <Card.Img variant="top" className="img-resize" src={L_SAIR} alt="SAir Air logo" />
              </Card.Header>
              <Card.Body className="d-flex justify-content-center flex-column">
                <div>
                  <Card.Title className="text-center">IT Officer</Card.Title>
                </div>
                <div>
                  <Card.Text className="text-center style">
                    <strong>Technology:</strong> Php, JS , Bootstrap
                    <br />
                    <strong>Duration:</strong> August 2019 - November 2019
                    <br/>
                    <ul className="text-left">
                      <li><strong>Developed and maintenance </strong> company website.</li>
                      <li><strong>Provided</strong> it support. 
                      </li>
                      {/* <li><strong>Co-created</strong> React Document used as a guide for new developers.</li> */}

                    </ul>
                    
                    
                  </Card.Text>
                </div>
              </Card.Body>
            </Card>
          </Tilt>
        </Container>
      </Jumbotron>
    </div>
  );
};

export default Experience;